@import url(https://fonts.googleapis.com/css?family=Roboto:400,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
html, body, #root {
  height: 100%;
}

.App {
  font-family: 'Roboto', sans-serif;
  min-height: 100%;
  background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0.75) 100%), url(/desafio-cumplo/static/media/sharon-mccutcheon-665638-unsplash.bd82bdbe.jpg);
  background-size: cover;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.DateInput_input {
  font-family: 'Roboto', sans-serif;
}

.DateRangePicker {
  margin: auto;
  margin-right: 25px;
}


header {
  height: 70px;
  line-height: 70px;
  background-color: #fff;
  color: #008489;
  font-size: 60px;
  font-weight: 300;
  text-align: left;
  display: -webkit-flex;
  display: flex;

}

.line-across {
  height: 0;
  border-top: 1px solid #008489;
  display: inline-block;
  margin: auto;
}

.line-1 {
  width: 30%;
}

.line-2 {
  width: 30%;
}

.picker-div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 100px;
}

.get-stats-btn {
  height: 48px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0 30px;
  line-height: 48px;
  background-color: #00a699;
  color: #fff;
  margin: auto;
  margin-left: 25px;
  border-radius: 5px;
  font-size: 20px;
}

.get-stats-btn:hover {
  cursor: pointer;
}

.chart-canvas {
    width: 70%;
    margin: auto;
}
